export default {
	name :"keyboard",
	data() {
		return {
			valueList:[]
		};
	},
	methods:{
		bindKeyEvent(val){
			var that = this;
			if(val!='delete'){
				if(that.valueList.length==6){
				   return
				}
				that.valueList.push(val);
				that.$emit('valueList',that.valueList)
			}else{
				if(that.valueList.length==0){
					 return
				}
				that.valueList.pop();
				that.$emit('valueList',that.valueList)
			}
			
		}
	}
}
